<template>
    <div class="market t-font">
        <!-- <BgItem010 /> -->
        <a-row>
            <a-col>
                <a-table class="fontSize" :columns="columns" :dataSource="tableDatas" :loading="loading" :pagination="pagination" @change="handleTableChange" :rowKey="tableDatas => tableDatas.id">
                    <a slot="symbol" slot-scope="text, record">
                        <img :src="record.img" alt="image" height="24">
                        {{ text }}
                    </a>
                </a-table>
            </a-col>
        </a-row>

    </div>
</template>


<script>
import BgItem010 from "@/components/BgItem/Bg010.vue";

export default {
    name: "Market",
    components: {
        BgItem010
    },
    data() {
        return {
            loading: false,

            tableDatas: [],
            pagination: {
                total: 0,
                pageSize: 50,
            },
            queryParam: {
                page: 1,
                size: 50,
            },
            columns: [
                {
                    title: "Name",
                    dataIndex: "symbol",
                    width: "10%",
                    scopedSlots: { customRender: 'symbol' },
                }, {
                    title: "Last Price",
                    dataIndex: "priceUsd",
                    width: "15%",
                }, {
                    title: "Change",
                    dataIndex: "changePercent24Hr",
                    width: "15%",
                }, {
                    title: "High",
                    dataIndex: "high",
                    width: "15%",
                }, {
                    title: "Low",
                    dataIndex: "low",
                    width: "15%",
                }, {
                    title: "24H Vol",
                    dataIndex: "volumeUsd24Hr",
                    width: "20%",
                    // scopedSlots: { customRender: 'status' },
                }
            ],

        };
    },
    destroyed: function () { },
    methods: {
        handleTableChange(pagination) {
            this.pagination.current = pagination.current;
            this.pagination.pageSize = pagination.pageSize;
            this.queryParam.page = pagination.current;
            this.queryParam.size = pagination.pageSize;
            // this.getTableList();
        },
        showList(res) {
            let _t = this;
            let ts = Math.floor(new Date().getTime() / 1000 / 100) * 100;
            let a = res.data.value;
            let i = 0
            a.forEach(function (item) {
                let symbol = item.symbol.toLocaleLowerCase();
                item.img = `https://assets.coincap.io/assets/icons/${symbol}@2x.png`;
                item.img7x24 = `https://images.cryptocompare.com/sparkchart/${item.symbol}/USD/latest.png?ts=${ts}`;
                // item.class = t.getClassName(item);
                item._id = i++
                item.priceUsd = _t.g.round(item.priceUsd, 1, 4)
                item.high = _t.g.round(0, 1, 4)
                item.low = _t.g.round(0, 1, 4)
                item.changePercent24Hr = _t.g.round(item.changePercent24Hr, 1, 4)
                item.volumeUsd24Hr = _t.g.round(item.volumeUsd24Hr, 1, 4)
            });
            this.tableDatas = a;
            console.log(this.tableDatas);
            // this.$EventBus.$emit("homeMarketListDataComplete", a);
        },
        initData() {
            $.get(
                `${this.g.apiUrl}/api/caches?key=coincap-coin-list`,
                (res) => {
                    this.loading = false;
                    if (res.flag != "ok") {
                        //提示错误
                        return;
                    }
                    this.showList(res);
                }
            );
        },
        init() {
            this.initData();
        },
    },
    mounted() {
        this.init();
    },
};
</script>



<style scoped>
.fontSize {
    font-size: 1rem;
    font-family: Verdana;
}
</style>